.carousel-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  display: flex; /* Adiciona flexbox para alinhamento */
  justify-content: center; /* Centraliza horizontalmente */
  align-items: center; /* Centraliza verticalmente */
}

.carousel-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.carousel-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  font-size: 2rem;
  padding: 10px 15px;
  border: none;
  cursor: pointer;
  z-index: 2;
}

.carousel-button:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1;
}

.carousel-text {
  position: absolute;
  color: white;
  text-align: center;
  z-index: 2;
  opacity: 0;
  animation: fadeIn 3s forwards;
}

.carousel-text h2 {
  font-size: 2.3rem;
  margin-bottom: 20px;
  font-weight: bold;
  line-height: 1.2;
  white-space: normal;
}

.carousel-button-text {
  padding: 10px 20px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  font-size: 1.2rem;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
  text-decoration: none;
}

.carousel-button-text:hover {
  background-color: rgba(0, 0, 0, 0.9);
}

.carousel-indicators {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
  z-index: 3;
}

.indicator {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  transition: background-color 0.3s;
}

.indicator:hover {
  background-color: rgba(255, 255, 255, 0.8);
}

.indicator.active {
  background-color: rgba(255, 255, 255, 1);
}

/* Animação do fade in com delay */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* Responsividade */
@media (max-width: 1024px) {
  .carousel-text h2 {
    font-size: 2rem;
    margin-bottom: 15px;
  }

  .carousel-button {
    font-size: 1.5rem;
  }

  .carousel-button-text {
    font-size: 1rem;
    padding: 8px 15px;
  }
}

@media (max-width: 768px) {
  .carousel-text h2 {
    font-size: 1.8rem;
    margin-bottom: 10px;
  }

  .carousel-button {
    font-size: 1.3rem;
  }

  .carousel-button-text {
    font-size: 0.9rem;
    padding: 8px 12px;
  }

  .carousel-indicators {
    bottom: 10px;
  }
}

@media (max-width: 480px) {
  .carousel-text h2 {
    font-size: 1.5rem;
  }

  .carousel-button {
    font-size: 1.2rem;
  }

  .carousel-button-text {
    font-size: 0.8rem;
    padding: 6px 10px;
  }

  .carousel-indicators {
    bottom: 5px;
  }
}
